import React, { useEffect, useState, useMemo } from 'react';
import axios from 'axios';
import DataTable from 'react-data-table-component';

export default function CreateForm(props) {
    const { match: { params } } = props;
    const form_id =  params.id;
    const [submissions, setSubmissions] = useState([]);

    const customStyles = {
        rows: {
          style: {
            minHeight: '80px', // override the row height
            fontSize:'17px',
          }
        },
        headCells: {
          style: {
            paddingLeft: '8px', // override the cell padding for head cells
            paddingRight: '8px',
          },
        },
        cells: {
          style: {
            paddingLeft: '8px', // override the cell padding for data cells
            paddingRight: '8px',
          },
        },
      };


    useEffect(()=>{
        axios.get(`/form/${form_id}/submissions`).then(res=>{
            setSubmissions(res.data.submissions)
        }).catch(err=>{
            console.log(err)
        })
    },[])

    const handleShow=(submission)=> {
        props.history.push(`/submission/${submission._id}`);

    };

    const columns = useMemo(
        () => [
        { name: "id", selector: "report_title", sortable: true },
        {
            name:"Actions",
            grow:2,
            center:true,
            compact:true,
            wrap:true,
            style:{

            },
            cell: (row,index,column,id) =>
            <>
             <button style={{'marginRight':'10px',"padding":"15px 26px","border":"none","background":"gray","color":"white","borderRadius":"50em","fontSize":"14px"}} onClick={()=>handleShow(row)}>Show</button>
            </>
        },
      ])

    return (
        <div>
            <DataTable
                    title="Forms"
                    columns={columns}
                    data={submissions}
                    defaultSortFieldId={1}
                    pagination
                    highlightOnHover
                    striped
                    pointerOnHover
                    customStyles={customStyles}
                    />

        </div>
    );

}
